import { FunctionalComponent, h } from "preact"
import { Route, Router, RouterOnChangeArgs } from "preact-router"
import { useEffect, useRef, useState } from "preact/hooks"
import Home from "../routes/home"
import NotFoundPage from "../routes/notfound"
import Profile from "../routes/profile"
export interface Vanta {
  destroy: () => void
}
const App: FunctionalComponent = () => {
  let currentUrl: string
  const handleRoute = (e: RouterOnChangeArgs) => {
    currentUrl = e.url
  }
  const appRef = useRef(null)
  const [vantaEffect, setVantaEffect] = useState<Vanta>(null)
  const NET = require("vanta/dist/vanta.net.min").default
  useEffect(() => {
    if (!vantaEffect)
      setVantaEffect(
        NET({
          el: appRef.current,
          mouseControls: true,
          touchControls: false,
          showDots: true,
          gyroControls: false,
          points: 7,
          mouseCoeffX: 4,
          mouseCoeffY: 4,
          maxDistance: 20,
          spacing: 20,
          minHeight: 200.0,
          minWidth: 200.0,
          scale: 1.2,
          scaleMobile: 1.0
        })
      )
    return () => vantaEffect?.destroy()
  }, [vantaEffect])
  return (
    <div id="app" ref={appRef} style="overflow: hidden;">
      <div style="z-index: 50; overflow: hidden;">
        <Router onChange={handleRoute}>
          <Route path="/" component={Home} />
          <Route path="/profile/" component={Profile} user="me" />
          <NotFoundPage default />
        </Router>
      </div>
    </div>
  )
}

export default App
